import { useEffect, useState } from "react";
import { Box } from "@mui/material";
import "../style/message.css";
import InputAdornment from '@mui/material/InputAdornment';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import InputLabel from "@mui/material/InputLabel";
import SendIcon from '@mui/icons-material/Send';

const ChatHistory = (props) => {
    const {handleUserPrompt} = props;
    const [history, setHistory] = useState(null);
    useEffect(() => {
        setHistory(props.history);
    }, [props.history]);

    return (
        <Box>
            <Box display={`flex`} mt={`20px`}>
                <FormControl fullWidth variant="outlined">
                    <InputLabel htmlFor="outlined-adornment-prompt">QA</InputLabel>
                    <OutlinedInput
                        id="outlined-adornment-prompt"
                        endAdornment={<InputAdornment position="end"><SendIcon /></InputAdornment>}
                        onKeyDown={handleUserPrompt}
                        onChange={handleUserPrompt}
                        label='QA'
                    />
                </FormControl>
            </Box>
            <Box className="chat-container" mt={`20px`}>
                <ul className="chat">
                    {history &&
                        history.length > 0 &&
                        history.map((row, index) => {
                            return (
                                <Box
                                    key={index}
                                    display="flex"
                                    flexDirection="column"
                                    alignItems="flex-start"
                                >
                                    <Box
                                        display="flex"
                                        justifyContent="center"
                                        alignItems="center"
                                        className="message flex"
                                        px='10px'
                                        py='2px'
                                    >
                                        <Box fontSize={38} fontFamily={`emoji`}>
                                            {" "}
                                            Q:
                                        </Box>
                                        <p style={{ marginLeft: "10px", marginRight: "10px" }}>{row[0]}</p>
                                    </Box>
                                    <Box
                                        display="flex"
                                        justifyContent="center"
                                        alignItems="center"
                                        className="message"
                                        p="10px"
                                    >
                                        <p style={{ marginLeft: "20px", marginRight: "5px" }}>{row[1]}</p>
                                        <Box fontSize={38} fontFamily={`emoji`}>
                                            {" "}
                                            :A
                                        </Box>
                                    </Box>
                                </Box>
                            );
                        })}
                </ul>
            </Box>
        </Box>
    );
};

export default ChatHistory;
